<template>
    <div>
        <mhead-top titleHead="我的订单" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/index/index" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="m-u-order">
                <div class="mot-box">
                    <div @click="changeType('all')" :class="activeName=='all'?'mon-item active':'mon-item'" >
                        全部
                    </div>
                    <div @click="changeType('weifukuan')" :class="activeName=='weifukuan'?'mon-item active':'mon-item'" >
                        待付款
                    </div>
                    <div @click="changeType('yifukuan')" :class="activeName=='yifukuan'?'mon-item active':'mon-item'" >
                        待收货
                    </div>
                    <div @click="changeType('daipinglun')" :class="activeName=='daipinglun'?'mon-item active':'mon-item'" >
                        待评价
                    </div>
                    <div @click="changeType('quxiao')" :class="activeName=='quxiao'?'mon-item active':'mon-item'" >
                        已取消
                    </div>
                </div>
                <el-collapse-transition>
                    <div class="mot-list" v-if="tableData.length!=0">
                        <div class="mot-inBox" v-for="(item,index) in tableData" :key="index">
                        <div class="mot-top">
                            <div class="mot-num">订单编号：{{item.order_code}}</div>
                            <div :class="item.state=='待付款'?'mot-state unfinish':item.state=='取消订单'?'mot-state color9':'mot-state finish'">{{item.state}}</div>
                        </div>
                        <div class="mot-pros" v-for="(i,cindex) in item.goods" :key="cindex" @click="toGoodDetail(i.specifications_id)">
                            <div class="mop-item">
                                <div class="mop-pic">
                                    <el-image class="mop-image" :src="i.pic"></el-image>
                                </div>
                                <div class="mop-info">
                                    <div class="mop-name">{{i.name}}</div>
                                    <div class="mop-spec">
                                        <span>规格：{{i.spec_name}}{{i.spec_unit}}</span>
                                    </div>
                                    <div class="flex f-nowrap j-bet">
                                        <div class="mop-price">￥{{i.price}}/{{i.supply_unit}}</div>
                                        <div class="mop-number">
                                            ×{{i.number}}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="mot-count">
                            合计：￥{{item.price_total}}
                        </div>
                        <div class="mot-control">
                            <el-button class="mot-btn" size="mini" @click="cancelOrder(item.order_id)" v-if="item.state=='待付款'">取消订单</el-button>
                            <el-button class="mot-btn" size="mini" @click="toDetail(item.order_id)">订单详情</el-button>
                            <el-button class="mot-btn" v-if="item.state=='配送中'" size="mini" @click="toLogistics(item.order_id)">查看物流</el-button>
                            <el-button class="mot-btn" size="mini" type="primary" plain @click="toReceive(item.order_id)" v-if="item.state=='配送中'||item.state=='已付款'">确认收货</el-button>
                            <el-button class="mot-btn" v-if="item.state=='已签收'&&item.is_comment==0" plain type="primary" size="mini" @click="toEvaluate(item.order_id)">评价</el-button>
                            <el-button type="text" v-if="item.state=='已签收'&&item.is_comment==1" disabled>已评价</el-button>
                            <el-button class="mot-btn" v-if="item.state=='待付款'" size="mini" type="warning" plain @click="payNow(item.order_id)">立即付款</el-button>
                        </div>
                    </div>
                    </div>
                    <div v-else>
                        <div class="mt50 f16 color6 tc">暂无数据</div>
                    </div>
                </el-collapse-transition>
                <div class="page">
                    <el-pagination
                            small
                            class="tc mt10"
                            @current-change="handleCurrentChange"
                            prev-text="上一页"
                            next-text="下一页"
                            layout="prev, pager, next"
                            :current-page="page"
                            :page-size="rows"
                            :pager-count="5"
                            :total="count">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {getMyOrderList,orderCancel,orderConfirm,getOpenIdToWx} from '../../../service/api';
    export default {
        name: 'mOrder',
        data(){
            return{
                activeName: 'all',
                timeOptions: [{
                    value: '选项1',
                    label: '近三个月订单'
                }, {
                    value: '选项2',
                    label: '2019年订单'
                }],
                timeValue: '近三个月订单',
                tableData: [],
                rows:4,//每页显示数
                page:1,//当前页面
                count:0,//总条数
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{

        },
        methods:{
            async initData(){
                if(this.$route.query.activeName){
                    this.changeType(this.$route.query.activeName);
                }else{
                    this.getList(this.activeName);
                }
            },
            async getList(activeTar){
                //yifukuan = yifukuan + peisong
                if(activeTar == 'yifukuan'){
                    var res1 = await getMyOrderList(
                        'yifukuan',//type
                        this.rows,
                        this.page
                    );
                    var res2 = await getMyOrderList(
                        'peisong',//type
                        this.rows,
                        this.page
                    );
                    this.tableData=res1.data;
                    for(var i = 0 ; i < res2.data.length ; i++){
                        this.tableData.push(res2.data[i]);
                    }
                    this.count = res1.count + res2.count;
                }else{
                    var res = await getMyOrderList(
                        activeTar=='all'?'':activeTar,
                        this.rows,
                        this.page
                    );
                    if(res.status!=12007){
                        this.count = res.count;
                        this.tableData = res.data;
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
                console.log("this.count:"+this.count);
                console.log("activeName:"+this.activeName);
            },
            toEvaluate(order_id){
                this.$router.push({path:"/userCenter/evaluate/evaluate",query:{order_id:order_id}});
            },
            toDetail(order_id){
                this.$router.push({path:"/userCenter/order/detail",query:{order_id:order_id}});
            },
            async changeType(tar){
                //待收货 type:peisong&yifukuan

                this.activeName = tar;
                this.page = 1;
                if(this.activeName=='yifukuan'){
                    this.getList('yifukuan');
                }else{
                    this.getList(tar);
                }
            },
            cancelOrder(order_id){
                this.$confirm('你确定要取消该订单吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderCancel(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.changeType(this.activeName);
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });

            },
            async payNow(orderId){
                var ua = window.navigator.userAgent.toLowerCase();
            　　if (ua.match(/MicroMessenger/i) == 'micromessenger'){
                    //是微信浏览器
                    var tar = await getOpenIdToWx(orderId);
                    window.location.href=tar.url;
            　　}else{
                    this.$router.push({path:"/order/pay",query:{orderId:orderId}});
                }
            },
            async toReceive(order_id){
                this.$confirm('您是否收到该订单商品?', '', {
                    confirmButtonText: '已收货',
                    cancelButtonText: '未收货',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderConfirm(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.changeType(this.activeName);
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });

            },
            async toLogistics(order_id){
                this.$router.push({path:"/userCenter/order/logistics",query:{order_id:order_id}});
            },
            toGoodDetail(spec_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:spec_id}});
            },
            handleCurrentChange(val) {
                //改变页码
                this.page = val;
                console.log(this.activeName);
                this.getList(this.activeName);
            }
        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*tab*/
    .m-u-order{
        height: px2rem(84);
        background-color: #fff;
        border-bottom: $bc;
        border-bottom:2px solid #fff;
    }
    .mot-box{
        background-color: #fff;
        display: flex;
        flex-wrap: nowrap;
        padding:0 px2rem(20);
        line-height: px2rem(84);
        justify-content: space-between;
        width:100%;
        position: fixed;
        top:px2rem(90);
        left:0;
        z-index: 1000;

    }
    .mon-item{
        width:20%;
        text-align: center;
        border-bottom:2px solid #fff;
    }
    .mon-item.active{
        color: $default;
        border-bottom:2px solid $default;
    }

    /*内容*/
    .mot-list{
        margin:px2rem(100) 0 px2rem(104) 0;
        padding-bottom:px2rem(30);
    }
    .mot-inBox{
        background-color: #fff;
        margin-top:px2rem(20);
        padding:0 px2rem(20);
    }
    .mot-top{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        padding:px2rem(10) 0 px2rem(6) 0;
        border-bottom:1px solid $bc;
        font-size: px2rem(28);
    }
    .mot-state.finish{
        color:$default;
    }
    .mot-state.unfinish{
        color: $colorprice;
    }
    /*产品*/
    .mop-item{
        background: #f5f5f5;
        padding:px2rem(10);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
        margin-top:px2rem(10);
    }
    .mop-pic{
        width:px2rem(110);
        height: px2rem(110);
        padding-right:px2rem(20);
    }
    .mop-image{
        width:px2rem(110);
        height: px2rem(110);
        object-fit: contain;
        background-color: #f2f2f2;
    }
    .mop-info{
        width:px2rem(510);
    }
    .mop-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(36);
        min-width: px2rem(460);
        font-size: px2rem(28);
        margin-top:5px;
    }
    .mop-spec{
        font-size:px2rem(24);
        color:#999;
        display: block;
        height: px2rem(36);
        line-height: px2rem(36);
        background-color: #f5f5f5;
    }
    .mop-spec span{
        display: inline-block;
        float:left;
    }
    .mop-spec .icon{
        font-size:px2rem(22);
        color:#999;
        height: px2rem(36);
        line-height: px2rem(36);
        padding-left:px2rem(6);
        float:left;
    }
    .mop-price{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    /*操作*/
    .mot-count{
        padding:px2rem(6) 0;
        font-size: px2rem(28);
        border-bottom:1px solid $bc;
        text-align: right;
    }
    .mot-control{
        text-align: right;
        padding:px2rem(10) 0;
    }
    .mot-btn{
        display: inline-block;
    }
    .page{
        width:100%;
        height: px2rem(104);
        position: fixed;
        bottom:0;
        left:0;
        background-color:$colorwhite;
        border-top:1px solid #eee;
        z-index: 10000;
    }


</style>



